#NEW_ALL_TABLE_Header {
    background-color: #272B35;
    width: 100%;
    height: 60px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    display: flex;
    align-items: center;
    padding: 0px 20px;
    color: white;
}

#NEW_ALL_TABLE_Header > span {
    font-weight: 600;
}

#NEW_ALL_TABLE_Spacer {
    margin: 60px 0px;
}

#NEW_ALL_TABLE_LOADING {
    height: 300px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#NEW_ALL_TABLE_LOADING > div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#NEW_ALL_TABLE_LOADING > div > div {
    margin-top: 30px;
}



/* Second Table Date Pickers General Reports */


.Second_TABLE_DATE_PICKERS_CODE_HEAD{
    width: 100%;
    padding: 0px 10px 10px;
    display: flex;
    justify-content: flex-end;
}

.Second_TABLE_DATE_PICKERS_CODE_HEAD > div > * {
    margin-left: 40px;
}