/* Style the tab */
.tab {
  overflow: hidden;
  background-color: rgb(39, 43, 54);

  /* border: 1px solid #ccc; */
  /* background-color: #f1f1f1; */
}

.active-tab {
  overflow: hidden;
  background-color: #3455B4
}



.inline-tab button {
  background-color: inherit;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
  border: 0.1px solid #868080;
}
.inline-tab-two button {
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
  border: 0.1px solid #868080;
  color: white;
  border-radius: 0px;
  font-size: 14px;
  font-weight: 600;
  display: flex;
}


.tab .tablinks-two {
  width: 100%;
  border: 0.1px solid #000;
  background-color: #272B35;
  color: #fff;
  text-align: left;
}
.inline-tab-two button:hover {
  background-color: #3b4152;
  color:white
}

.inline-tab-two button:active {
  background-color: #43547d;
  color:white
 
}

.tab .tablinks {
  width: 100%;
  border: 0.1px solid #000;
  background-color: #272B35;
  color: #fff;
  text-align: left;
}
.innerTab {
  background-color: rgb(170, 170, 170);
  padding: 5px 0px 5px 16px;
  color: #fff;
  cursor: pointer;
  border: 0.1px solid #868080;
}
