#client_External_Screen {
    padding: 20px;
}

#client_External_Screen > h1 {
    font-size: xx-large;
    font-weight: bold;
    color: #1B1E25;
    box-shadow: darkblue;
}

#client_External_Screen > #SEARCH_FEILDS_HERE {
    flex-grow: 1;
    margin: 20px 0px;
}

#client_External_Screen > #SEARCH_FEILDS_HERE .button_class {
   /* transform: translateY(25%); */
   height: 100%;
   font-size: 18px;
}
 
#client_External_Screen > #Actual_Content_Around_ID {
    margin: 20px 0px;
}

#client_External_Screen > #Actual_Content_Around_ID > .TEXT_FEILDS_CONTENT{
    border: 1px dashed black;
    margin: 10px 0px;
    padding: 10px 10px;
    border-top-right-radius: 40px;
    border-bottom-right-radius: 40px;
}
#client_External_Screen > #Actual_Content_Around_ID .TEXT_FEILDS_CONTENT_2{
    border: 1px dashed black;
    margin: 10px 0px;
    padding: 10px 10px;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 40px;
}

#client_External_Screen > #Actual_Content_Around_ID .TEXT_FEILDS_CONTENT_2 > p {
    color: grey;
}

#client_External_Screen > #Actual_Content_Around_ID > .TEXT_FEILDS_CONTENT >h1 , .TEXT_FEILDS_CONTENT_2 >h1 {
    font-size: 16px;
    font-weight: bold;
}


#Images_Wrapper_HERE_MERE {
    border: 1px dashed black;
    margin: 10px 0px;
    padding: 10px 10px;
    border-top-right-radius: 40px;
}

#Images_Wrapper_HERE_MERE > div {
    display: inline-block;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
}

#Images_Wrapper_HERE_MERE > h1 {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 20px;
}

#Images_Wrapper_HERE_MERE #PER_PREVIEW > *{
    height: 60px;
    width: 60px;
    margin: 10px;
}

#ERROR_MESSAGE_ALL{
    margin-top: 20px;
    font-size: 20px;
    font-style: italic;
}

#LOADING_DATA_ProjectData{
    height: 400px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#LOADING_DATA_ProjectData > div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#LOADING_DATA_ProjectData > div > div {
    margin-top: 10px;
}